import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GenerateReportPage } from './generate-report.page';


const routes: Routes = [

  {path: '', component: GenerateReportPage, pathMatch: 'full'},
  {path: 'data/:report', loadChildren: () => import('./generate-report-data/generate-report-data.module')
  .then( m => m.GenerateReportDataModule)},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class GenerateReportRoutingModule { }
