import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { GeneralService } from 'src/app/general.service';
import swal from 'sweetalert';
import { ReportService } from './report.service';

@Component({
  selector: 'app-generate-report',
  templateUrl: './generate-report.page.html',
  styleUrls: ['./generate-report.page.css']
})
// tslint:disable-next-line: component-class-suffix
export class GenerateReportPage implements OnInit {

  constructor(
    private readonly router: Router,
    private readonly route: ActivatedRoute,
    private readonly reportService: ReportService,
    private readonly generalService: GeneralService
  ) { }

  ngOnInit(): void {
  }

  // tslint:disable-next-line: typedef
  navigateToData(pathParam: string) {
    this.router.navigate(['data', pathParam], {relativeTo: this.route});
  }

  generateReport() {
    this.generalService.updateBlockedUI(true);
    this.reportService.generateReport().subscribe(() => {
      this.generalService.updateBlockedUI(false);
      swal({
        title: 'Report generated',
        icon: 'success',
      });
    }, err => {
      this.generalService.updateBlockedUI(false);
      swal({
        title: 'Failed to generate Report',
        icon: 'error',
      });
    });
  }



}
